    const fetchRunpodServiceMJv4 = async (prompt: string, total: number, callback: any) => {
        const skeletons = [{height: 1, minHeight: '150px'}, {height: 2, minHeight: '150px'}, {height: 3, minHeight: '150px'}, {height: 4, minHeight: '150px'}];
        if (total === 1) {
            setDisplayMJv4Images(skeletons);
        }
        if (total === 4) {
            setDisplayMJv4Images(prevState => {
                return Array.isArray(prevState) ? [...prevState, ...skeletons] : skeletons;
            });
        }
        RunPodDiffusionService(prompt, false, false, total, ProductionModelList.MIDJOURNEY_v4).then(convertResMJv4 => {
            setDisplayMJv4Images((previousState) => {
                convertResMJv4 = wrapOnClickEventForData(convertResMJv4, '150px', '');
                let newResult: any = [];
                if (previousState === null) return convertResMJv4;
                if (total === 1) {
                    newResult = [...previousState];
                    newResult[0] = convertResMJv4[0];
                } else if (total === 3) {
                    newResult = [...previousState];
                    for (let i = 0; i < convertResMJv4.length; i++) {
                        newResult[i + 1] = convertResMJv4[i];
                    }
                } else {
                    newResult = [...previousState];
                    if (convertResMJv4[3]) {
                        newResult[newResult.length - 1] = convertResMJv4[3];
                    }
                    if (convertResMJv4[2]) {
                        newResult[newResult.length - 2] = convertResMJv4[2];
                    }
                    if (convertResMJv4[1]) {
                        newResult[newResult.length - 3] = convertResMJv4[1];
                    }
                    if (convertResMJv4[0]) {
                        newResult[newResult.length - 4] = convertResMJv4[0];
                    }
                }
                return newResult;
            });
        }).catch((error) => {
            toast.error("Unknown error, please contact developer...");
            console.log("Error at fetchRunpodService: ", error);
        }).finally(() => {
            if (callback) {
                callback();
            }
        });
    }

