    const handleSearchParallel = async (value: string) => {
        if (!value) {
            setIsSubmit(false);
            setDisplayLexicaImages(null);
            await fetchTrend();

            return;
        }

        setIsLoadingLexica(true);
        // setIsLoadingSD(false);
        // setIsLoadingMJ(false);
        setIsSubmit(true);
        setDisplayTrendingImages(null);

        // Priority result from Lexica first 
        setDisplayLexicaImages(null);
        let result_lexica: any = await LexicaSearchService(value ? value : prompt);
        result_lexica = wrapOnClickEventForData(result_lexica, '', 'lexica');
        if (!isLexicaResponse(result_lexica)) {
            setDisplayLexicaImages(result_lexica);
            setSuggestStyles(StyleSuggestion(result_lexica, 10));
        }
        setIsLoadingLexica(false);

        // After that, get result from SD API all same time
        // fastInferenceV2(value ? value : prompt);
        slowInferenceV2(value ? value : prompt);
    }

