import PropTypes from 'prop-types';
import { useState } from 'react';

import {
  CardItem,
  ImageItem,
  HeartBtn,
  MoreActionContainer,
} from './index.styled';

import HeartIcon from '@components/Icons/HeartIcon';
import MoreAction from '@components/MoreAction';
import EditIcon from '@components/Icons/EditIcon';
// import PictureIcon from '@components/Icons/PictureIcon';
import DownloadIcon from '@components/Icons/DownloadIcon';
import ShareIcon from '@components/Icons/ShareIcon';

import LexicaLogo from '@assets/LexicaLogo.svg';
import OpenArtLogo from '@/assets/OpenArtLogo.svg';
import ShortIconWhite from '@/assets/ShortIconWhite.svg';


type MasonryItemProps = PropTypes.InferProps<typeof propTypes>;

const propTypes = {
  data: PropTypes.shape({
    id: PropTypes.number,
    caption: PropTypes.string,
    src: PropTypes.string,
    srcSmall: PropTypes.string,
    onClickItem: PropTypes.func,
    onRemix: PropTypes.func,
    minHeight: PropTypes.string.isRequired,
    source: PropTypes.string,
  }).isRequired
};

const actions = [
  {
    value: 'remix',
    label: 'Remix',
    icon: <EditIcon />
  },
  // {
  //   value: 'more_like_this',
  //   label: 'More like this',
  //   icon: <PictureIcon />
  // },
  {
    value: 'download',
    label: 'Download',
    icon: <DownloadIcon />
  },
  {
    value: 'share',
    label: 'Share',
    icon: <ShareIcon />
  }
];

const MasonryItem = ({
  data: { src, srcSmall, caption, onClickItem, onRemix, minHeight, source }, data
}: MasonryItemProps) => {
  const [active, setActive] = useState(false);

  const handleActive = (e: any) => {
    e.stopPropagation();
    setActive(!active);
  }

  const handleClickItem = () => {
    if (onClickItem) onClickItem(data);
  };

  const handleClickMoreAction = (e: any) => {
    e.stopPropagation();
  };

  const handleSelectMoreAction = (e: any) => {
    if (e.value === "remix") {
      if (onRemix) onRemix(data.caption); 
    }
    else if (e.value === "download") {
      const anchor: any = document.createElement('a');
      if (src?.includes('webp')) {
          // TODO: below ony view new tab
        const anchor: any = document.createElement('a');
        anchor.href = src;
        anchor.download = src;
        anchor.target = "_blank";
        document.body.appendChild(anchor);
        anchor.click();
      } else {
        anchor.href = src;
        anchor.download = src;
        document.body.appendChild(anchor);
        anchor.click();
      }
      document.body.removeChild(anchor);
    }
  };

  const renderSourceImg = () => {
    switch (source) {
      case 'lexica':
        return <img src={LexicaLogo} alt='Lexica' />;
      case 'openart':
        return <img src={OpenArtLogo} alt='OpenArt' />;
      default:
        return <img src={ShortIconWhite} style={{ height: 32 }} alt='Galerie' />;
    }
  };

  return (
    <CardItem onClick={handleClickItem} minHeight={minHeight}>
      {src && <ImageItem alt={src} src={srcSmall || src} />}
      <div className="overlay-social">
        <div>
          {renderSourceImg()}
        </div>
        <MoreActionContainer>
          <MoreAction data={actions} onSelect={handleSelectMoreAction} onClick={handleClickMoreAction} />
          <HeartBtn onClick={handleActive}>
            <HeartIcon active={active} />
          </HeartBtn>
        </MoreActionContainer>
      </div>
      <div className="overlay-caption">
        {caption && caption.length > 45 ? caption?.slice(0, 45) + '...' : caption}
      </div>
    </CardItem>
  );
};

MasonryItem.propTypes = propTypes;

MasonryItem.defaultProps = {
  data: {
    id: 0,
    src: '',
    caption: '',
    srcSmall: '',
    minHeight: '',
    source: '',
    onClickItem: () => { },
    onRemix: () => { },
  }
}

export default MasonryItem;
