import { useState, useContext, useEffect } from 'react';
import {
  Nav,
  Actions,
  // SignInBtn,
  // SocialBtn,
  // RatingBtn,
} from './index.styled';

import { MAX_Z_INDEX } from '@config/const';

import Logo from '@assets/Logo.svg';

import HamburgerBtn from '@components/HamburgerBtn';
import MobileMenu from '@components/MobileMenu';
// import InstagramIcon from '@components/Icons/InstagramIcon';
// import TwitterIcon from '@components/Icons/TwitterIcon';

import useSticky from '@hooks/useSticky';
import { AppContext } from '@context/AppContext';

const Navbar = () => {
  const { isOpenModal } = useContext(AppContext);
  const [toggleMobileMenu, setToggleMobileMenu] = useState(false);
  const { sticky, stickyRef } = useSticky();

  useEffect(() => {
    stickyRef.current.style.zIndex = isOpenModal ? 0 : MAX_Z_INDEX;
  }, [isOpenModal, stickyRef]);

  const handleToggleMobileMenu = (toggle: boolean) => {
    setToggleMobileMenu(toggle);
  };

  return (
    <Nav ref={stickyRef} className={`${sticky ? 'sticky' : ''}`}>
      {!isOpenModal && <img className={`${sticky ? 'logo sticky' : 'logo'}`} alt="Logo" src={Logo}
        onClick={() => window.location.href = "/"} />}
      <Actions className={`${sticky ? 'sticky' : ''}`}>
        {/* <RatingBtn>Contact us</RatingBtn> */}
        {/* <SocialBtn>
          <InstagramIcon />
        </SocialBtn>
        <SocialBtn>
          <TwitterIcon />
        </SocialBtn>
        <SignInBtn>+ Create</SignInBtn> */}
      </Actions>
      <HamburgerBtn toggle={toggleMobileMenu} onToggle={handleToggleMobileMenu} />
      {toggleMobileMenu && <MobileMenu />}
    </Nav>
  );
};

export default Navbar;
